"use client";

import Image from "next/image";
import type { PersonalityType, Recommendation } from "@/lib/types";
import { Button } from "./ui/button";
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "./ui/card";
import { ArrowRight, CheckCircle2, RefreshCw, ShieldCheck } from "lucide-react";
import Link from "next/link";
import { Badge } from "./ui/badge";

interface QuizResultsProps {
  personalityType: PersonalityType;
  recommendations: Recommendation[];
  onRestart: () => void;
}

const RationaleDisplay = ({ rationale }: { rationale: string }) => {
    const parts = rationale.split(/(However|but|On the other hand)/i);
    const goodFit = parts[0];
    const badFit = parts.length > 1 ? parts.slice(1).join('') : '';

    return (
        <div className="space-y-3 text-sm">
            <div className="flex items-start gap-2">
                <CheckCircle2 className="w-5 h-5 text-green-500 mt-0.5 shrink-0" />
                <p className="text-muted-foreground">{goodFit}</p>
            </div>
            {badFit && (
                 <div className="flex items-start gap-2">
                    <CheckCircle2 className="w-5 h-5 text-orange-500 mt-0.5 shrink-0" />
                    <p className="text-muted-foreground">{badFit}</p>
                </div>
            )}
        </div>
    );
};


export default function QuizResults({ personalityType, recommendations, onRestart }: QuizResultsProps) {
  return (
    <div>
      <div className="text-center mb-8">
        <h2 className="text-base font-semibold text-primary tracking-wide uppercase">Your Personality Type</h2>
        <p className="mt-2 text-4xl font-extrabold tracking-tight sm:text-5xl text-gradient">
          {personalityType.name}
        </p>
        <p className="mt-4 max-w-xl mx-auto text-lg text-muted-foreground">
          {personalityType.description}
        </p>
      </div>

      <div className="space-y-6">
        {recommendations.map((rec, index) => (
          <Card key={rec.id} className="glowing-card overflow-hidden">
            <div className="md:flex">
              <div className="md:w-1/3 md:shrink-0">
                <Image
                  src={rec.image.imageUrl}
                  alt={rec.name}
                  width={600}
                  height={400}
                  data-ai-hint={rec.image.imageHint}
                  className="h-48 w-full object-cover md:h-full"
                />
              </div>
              <div className="p-6 flex flex-col flex-1">
                <CardHeader className="p-0 mb-4">
                  <div className="flex justify-between items-start">
                    <CardTitle className="text-2xl font-bold">{rec.name}</CardTitle>
                    {index === 0 && (
                      <Badge variant="default" className="flex items-center gap-1">
                        <ShieldCheck className="w-4 h-4" />
                        Top Match
                      </Badge>
                    )}
                  </div>
                  <CardDescription>{rec.description}</CardDescription>
                </CardHeader>
                <CardContent className="p-0 flex-1">
                  <h4 className="font-semibold mb-2">AI-Powered Rationale:</h4>
                  <RationaleDisplay rationale={rec.rationale} />
                </CardContent>
                <CardFooter className="p-0 mt-6">
                  <Button asChild>
                    <Link href={rec.reviewUrl} target="_blank" rel="noopener noreferrer">
                      Explore Reviews <ArrowRight className="ml-2 h-4 w-4" />
                    </Link>
                  </Button>
                </CardFooter>
              </div>
            </div>
          </Card>
        ))}
      </div>

      <div className="mt-8 text-center">
        <Button variant="outline" onClick={onRestart}>
          <RefreshCw className="mr-2 h-4 w-4" />
          Take the Quiz Again
        </Button>
      </div>
    </div>
  );
}
